<template>
<CRow>
    <CCol col="12" class="mb-5">
        <CRow>
            <CCol col="12">
                <h3 class="font-weight-bold text-uppercase mb-1">Users</h3>
                <p class="text-primary text-sm-left mb-0">{{$options.identitas[0].instansi}}</p>
                <p class="text-primary text-sm-left">{{$options.identitas[0].provinsi}}</p>
            </CCol>
        </CRow>
    </CCol>
    <CCol col="12">
        <tableta :table="table" :isLoading=table.isLoading :data="table.data" :header="table.header" :rows="table.rows" :ButtonNew="table.new" :ButtonEdit="table.edit" :refresh="table.refresh" :ButtonDelete="table.delete" :search="table.search" />
    </CCol>
</CRow>
</template>

<script>
import identitas from '@/containers/_identitas'
import tableta from '@/views/table/table'
const axios = require('axios');
export default {
    name: 'Modul',
    identitas,
    components: {
        tableta
    },
    data() {
        return {
            table: {
                header: ['email','instansi','role'],
                rows: ['email','instansi.instansi','role.role'],
                data: [],
                activeItem: null,
                opened: [],
                search: null,
                isLoading: true,
                new: {
                    url: 'users/formulir/tambah/new',
                    name: 'Tambah Data',
                },
                edit: {
                    url: 'users/formulir/edit',
                    name: 'Edit Data',
                    params: 'id'
                },
                delete: {
                    url: this.$options.identitas[0].url + '/users?type=delete',
                    params: 'id',
                    dialog: 'email'
                },
                refresh: this.$options.identitas[0].url + '/users?type=dataByAll'
            }
        }
    },
    created() {
        this.getData()
    },
    methods: {
        getData() {
            axios.post(this.$options.identitas[0].url + '/users?type=dataByAll')
                .then((e) => {
                    this.table.data = e.data.data
                    this.table.isLoading = false
                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        }
    }

}
</script>
