<template>
<CRow>
    <CCol>
        <CRow>
            <CHeaderNav class="ml-3 d-md-down-none mr-auto">
                <CInput placeholder="Pencarian" size="sm" class="mt-1" v-model="table.search">
                    <template #prepend-content>
                        <CIcon name="cil-search" /></template>
                </CInput>
            </CHeaderNav>
            <CHeaderNav class="mr-0">
                <CHeaderNavItem class="">
                    <CButton color="info" size="sm" class="mr-1 text-white">
                        <router-link :to="{ path: ButtonNew.url}" class="text-white">
                            <CIcon name="cil-note-add" class="mr-1 "></CIcon> {{ButtonNew.name}}
                        </router-link>
                    </CButton>
                </CHeaderNavItem>
                <CHeaderNavItem class=" mr-2">
                    <CHeaderNavLink>
                        <CButton color="light" size="sm" class="mr-1 text-white" v-if="action.show">
                            <router-link :to="{ path: ButtonEdit.url + '/' + table.data[selected][ButtonEdit.params]}">
                                <CIcon name="cil-color-border" class="mr-1 "></CIcon> {{ButtonEdit.name}}
                            </router-link>
                        </CButton>
                        <CButton color="light" size="sm" class="mr-1 text-danger" v-if="action.show" @click="dialogDelete = true">
                            <CIcon name="cil-trash" class="mr-1"></CIcon> Hapus
                        </CButton>
                        <CDropdown color="light" size="sm">
                            <template slot="toggler-content">
                                <CIcon name="cil-list-filter" class="mr-1"></CIcon> Filter
                            </template>
                            <CDropdownItem>
                                sdsa
                            </CDropdownItem>
                            <CDropdownItem>Ubah Data</CDropdownItem>
                            <CDropdownItem>Hapus Data</CDropdownItem>
                        </CDropdown>
                        <CButton color="light" size="sm" @click="refreshTable()">
                            <CIcon name="cil-reload" class="mr-1"></CIcon> Muat Ulang
                        </CButton>
                    </CHeaderNavLink>
                </CHeaderNavItem>
            </CHeaderNav>
        </CRow>
    </CCol>
    <CCol col="12">
        <!-- <pre>{{tableRows}}</pre> -->

        <div class="border-top"></div>
        <div class="wrapperHead mt-3">
            <div class="tableFixHead" :style="{height : tableRows.length <= 10 ?  ($options.identitas[0].wHeight - 165) + 'px' : $options.identitas[0].wHeight + 'px'}">
                <table class="table table-sm table-hover">
                    <thead>
                        <tr>
                            <th width="20"></th>
                            <th v-for="(header, $index) in tableHeaders" :key="$index">
                                {{header.identifier}}
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-if="isLoading">
                            <td :colspan="(tableHeaders.length + 1)" class="pt-5">
                                <center>
                                    <CSpinner color="primary" class="mr-1" size="sm" /> Memuat Data ...
                                </center>
                            </td>
                        </tr>
                        <template v-for="(row,$rindex) in tableRows">
                            <tr :key="$rindex" :class="{ 'table-success' : $rindex === selected}">
                                <td class="font-weight-bold">
                                    <input type="checkbox" :value="$rindex" readonly :checked="$rindex === selected" @click="ClickTableOn($rindex)">
                                </td>
                                <td v-for="(cell,$index) in row" :key="$index" @click="ClickTableOn($rindex)">
                                    {{cell.value}}
                                </td>
                            </tr>
                            <!-- <tr :key="'ke-'+$rindex" v-if="table.opened.includes($rindex)">
                            <td :colspan="row.length+1">
                                {{table.data[$rindex]}}
                            </td>
                        </tr> -->
                        </template>
                    </tbody>
                </table>
            </div>
            <div class="footer border-top">
                <p class="text-muted mb-0" @click="toasat()">Total Data : {{tableRows.length}}</p>
                <CToaster :autohide="3000" v-if="toasNot">
                    <CToast :show.sync="toasNot"  header="Information" position="static" color="success">
                       Data Berhasil Di Hapus
                    </CToast>
                </CToaster>
            </div>
        </div>
    </CCol>
    <CModal title="Informasi" color="danger" :show.sync="dialogDelete" :closeOnBackdrop="false">
        Apakah Anda Yakin Menghapus Data : <p v-if="dialogDelete" class="font-weight-bold"> {{table.data[selected][ButtonDelete.dialog]}} ? </p>
        <template slot="footer">
            <CButton color="danger" @click="hapusdata()">Hapus</CButton>
        </template>
    </CModal>
</CRow>
</template>

<script>
import identitas from '@/containers/_identitas'
const axios = require('axios');
export default {
    identitas,
    data() {
        return {
            action: {
                show: false
            },
            selected: null,
            activeItem: null,
            dialogDelete: false,
            toasNot: false
        }
    },
    mounted() {
        // this.$options.identitas[0].wHeight - 200

    },
    computed: {
        tableHeaders() {
            return this.header.map((header, index) => {
                return {
                    index: index,
                    identifier: header,
                };
            });
        },
        tableRows() {
            var result = this.data.map((value, index) => {
                return this.rows.map((vle, indx) => {
                    var split = vle.split('.')
                    var a = {
                        key: vle,
                        value: value[split[0]][split[1]]
                    }
                    var b = {
                        key: vle,
                        value: value[split],

                    }
                    return split.length > 1 ? a : b

                })
            });
            if (!this.search) return result;

            const filterValue = this.search.toLowerCase();
            const filter = (event, index) => {
                let foundCategory = event.findIndex((c) => {
                    return c.value.toString().toLowerCase().includes(filterValue)
                })
                return foundCategory !== -1

            };
            return result.filter(filter);

        },
    },
    props: {
        data: Array,
        table: Object,
        search: String,
        refresh: String,
        header: Array,
        rows: Array,
        ButtonNew: Object,
        ButtonEdit: Object,
        ButtonDelete: Object,
        isLoading: Boolean,
    },
    methods: {
        ClickTableOn(id, active) {
            this.selected === id ? (this.selected = null, this.action.show = false) : (this.selected = id, this.action.show = true)
            const index = this.table.opened.indexOf(id);
            if (index > -1) {
                this.table.opened.splice(index, 1)
            } else {
                this.table.opened.push(id)
            }
        },
        refreshTable() {
            axios.post(this.refresh)
                .then((e) => {
                    this.table.data = e.data.data
                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        hapusdata(id) {
            axios.post(this.ButtonDelete.url, {
                    id: this.table.data[this.selected][this.ButtonDelete.params]
                })
                .then((e) => {
                    this.refreshTable()
                    this.action.show = false
                    this.dialogDelete = false
                    this.toasNot = true
                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },

    }

}
</script>

<style>
.wrapperHead {
    border: 1px solid #dfdfdf;
}

.wrapperHead .footer {
    background: white !important;
    padding: 8px 5px 8px 8px;
    font-size: 12px;

}

.tableFixHead {
    overflow-y: auto;
    font-size: 13px;
}

.tableFixHead thead th {
    position: sticky;
    top: 0;
    background: white !important;
    padding: 8px;
    border-top: none;

}

.tableFixHead tbody td {
    padding: 5px 8px 5px 8px;
}
</style>
